import React from 'react';
import { Field, reduxForm } from 'redux-form';
import { Input, Textarea } from '../Common/FormsContorls/FormsControls';
import s from './Profile.module.css';
const ProfileDataForm = (props) => {
  return (
    <form className={s.items} onSubmit={props.handleSubmit}>
      <button>Save</button>
      {props.error && <div>{props.error}</div>}
      <div>
        <b>Моё полное имя: </b>
        <Field component={Input} placeholder={'Full Name'} name={'fullName'} />
      </div>
      <div>
        <b>Ищю работу:</b>
        <Field name={'lookingForAJob'} component={Input} type={'checkbox'} />
      </div>
      <div>
        <b>Мои скиллы:</b>
        <Field name={'lookingForAJobDescription'} component={Textarea} />
      </div>

      <div>
        <b>Обо мне:</b> <Field name={'aboutMe'} component={Textarea} placeholder={'About Me'} />
      </div>
      <div>
        <b>Contacts:</b>
        {Object.keys(props.profile.contacts).map((key) => {
          return (
            <div>
              {key}: <Field name={'contacts.' + key} component={Input} />
            </div>
          );
        })}
      </div>
    </form>
  );
};
const ProfileDataReduxForm = reduxForm({ form: 'saveProfile' })(ProfileDataForm);
export default ProfileDataReduxForm;
