import { stopSubmit } from 'redux-form';
import { usersApi } from '../../api/api';

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_PROFILE_STATUS = 'SET_PROFILE_STATUS';
const UPLOAD_PHOTO = 'UPLOAD_PHOTO';

let initialState = {
  newPostText: 'GGMU!',
  posts: [
    {
      id: 1,
      text: 'Its my first post',
      count: 15,
    },
    {
      id: 2,
      text: 'Hi',
      count: 6,
    },
    {
      id: 3,
      text: 'Today is a good day',
      count: 35,
    },
  ],
  userProfile: null,
  profileStatus: null,
};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST:
      let newPost = {
        id: Date.now(),
        text: action.postText,
        count: 15,
      };

      return {
        ...state,
        posts: [...state.posts, newPost],
        newPostText: '',
      };
    case SET_USER_PROFILE:
      return {
        ...state,
        userProfile: action.profile,
      };
    case SET_PROFILE_STATUS:
      return {
        ...state,
        profileStatus: action.status,
      };
    case UPLOAD_PHOTO:
      return {
        ...state,
        userProfile: { ...state.userProfile, photos: action.photos },
      };
    default:
      return state;
  }
};
export const addPostActionCreator = (text) => {
  return {
    type: ADD_POST,
    postText: text,
  };
};

export const setUserProfile = (profile) => {
  return {
    type: SET_USER_PROFILE,
    profile,
  };
};
export const setProfileStatusAC = (status) => {
  return {
    type: SET_PROFILE_STATUS,
    status,
  };
};
export const setPhotosSuccess = (photos) => {
  return {
    type: UPLOAD_PHOTO,
    photos,
  };
};
export const setProfile = (userId) => async (dispatch) => {
  let data = await usersApi.getProfile(userId);
  dispatch(setUserProfile(data));
};
export const setStatus = (userId) => async (dispatch) => {
  let data = await usersApi.setProfileStatus(userId);
  dispatch(setProfileStatusAC(data));
};

export const updateStatus = (status) => async (dispatch) => {
  let data = await usersApi.updateStatus(status);
  if (data.resultCode === 0) {
    dispatch(setProfileStatusAC(status));
  }
};
export const uploadPhotos = (photos) => async (dispatch) => {
  let data = await usersApi.uploadPhotos(photos);
  if (data.resultCode === 0) {
    dispatch(setPhotosSuccess(data.data.photos));
  }
};
export const saveProfile = (profile) => async (dispatch, getState) => {
  const userId = getState().auth.userId;
  let data = await usersApi.saveProfile(profile);
  if (data.resultCode === 0) {
    dispatch(setProfile(userId));
  } else {
    dispatch(stopSubmit('saveProfile', { _error: data.messages[0] }));
    return Response.reject(data.messages[0]);
  }
};

export default profileReducer;
