import React from 'react';
import Preloader from '../Users/Preloader';
import Sancho from '../../assets/Images/Sancho.jpg';
import ProfileStatusWihHooks from './ProfileStatusWihHooks';
import s from './Profile.module.css';
import { useState } from 'react';
import ProfileData from './ProfileData';
import ProfileDataForm from './ProfileDataForm';
const ProfileInfo = (props) => {
  let [editMode, setEditMode] = useState(false);
  let onPhotoSelected = (e) => {
    if (e.target.files.length) {
      props.uploadPhotos(e.target.files[0]);
    }
  };

  if (!props.profile) {
    return <Preloader />;
  }
  const onSubmit = (formData) => {
    props.saveProfile(formData).then(() => {
      setEditMode(false);
    });
    console.log(formData.lookingForAJob);
  };
  return (
    <div className={s.body}>
      <div className={s.photo}>
        <img src={props.profile.photos.large || Sancho} alt="User's ava" />
        {props.isOwner && <input type="file" onChange={onPhotoSelected} />}
      </div>
      <div className={s.items}>
        <ProfileStatusWihHooks {...props} />
        <div>
          {editMode ? (
            <ProfileDataForm {...props} onSubmit={onSubmit} initialValues={props.profile} />
          ) : (
            <ProfileData
              {...props}
              goToEditMode={() => {
                setEditMode(true);
              }}
            />
          )}
        </div>
      </div>
    </div>
  );
};

export default ProfileInfo;
